/**
 * @public
 */
export function IMap(spec) {
    return {
        serialize(value, builder) {
            for (const key in spec) {
                // TODO: as any
                spec[key].serialize(value[key], builder);
            }
        },
        deserialize(reader) {
            const newValue = {};
            for (const key in spec) {
                // TODO: as any
                ;
                newValue[key] = spec[key].deserialize(reader);
            }
            return newValue;
        },
        create() {
            const newValue = {};
            for (const key in spec) {
                // TODO: as any
                ;
                newValue[key] = spec[key].create();
            }
            return newValue;
        }
    };
}
