/**
 * @public
 */
export const FlatString = {
    serialize(value, builder) {
        builder.writeBuffer(new TextEncoder().encode(value));
    },
    deserialize(reader) {
        return new TextDecoder().decode(reader.readBuffer());
    },
    create() {
        return '';
    }
};
/**
 * @public
 */
export const EcsString = FlatString;
