import * as MeshRendererSchema from './../generated/MeshRenderer.gen';
export function defineMeshRendererComponent(engine) {
    const MeshRenderer = engine.getComponent(MeshRendererSchema.COMPONENT_ID);
    return {
        ...MeshRenderer,
        setBox(entity, uvs) {
            MeshRenderer.createOrReplace(entity, {
                mesh: { $case: 'box', box: { uvs: uvs || [] } }
            });
        },
        setPlane(entity, uvs) {
            MeshRenderer.createOrReplace(entity, {
                mesh: { $case: 'plane', plane: { uvs: uvs || [] } }
            });
        },
        setCylinder(entity, radiusBottom, radiusTop) {
            MeshRenderer.createOrReplace(entity, {
                mesh: { $case: 'cylinder', cylinder: { radiusBottom, radiusTop } }
            });
        },
        setSphere(entity) {
            MeshRenderer.createOrReplace(entity, {
                mesh: { $case: 'sphere', sphere: {} }
            });
        }
    };
}
