function getAndClean(value) {
    const messagesToProcess = Array.from(value);
    value.length = 0;
    return messagesToProcess;
}
export function taskSystem(engine) {
    const tasks = [];
    async function runTask(task) {
        try {
            const resp = await task();
            return resp;
        }
        catch (e) {
            throw e;
            // TODO: dcl.error(`executeTask: FAILED. ${e.toString()}`, e)
        }
    }
    function executeTasks() {
        for (const task of getAndClean(tasks)) {
            runTask(task).catch(() => { });
        }
    }
    engine.addSystem(executeTasks);
    return {
        executeTask(task) {
            tasks.push(task);
        }
    };
}
